input[type="text"],
input[type="password"] {
  height: 35px;
  border: 1px solid rgba(0, 0, 0, 0.2);
}

.button-container {
  display: flex;
  justify-content: center;
}

.login-form {
  background-color: #2c3342;
  width: 25%;
  padding: 2rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.list-container {
  display: flex;
}

.error {
  color: red;
  font-size: 12px;
}

.title {
  font-size: 25px;
  margin-bottom: 20px;
}

.input-container {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin: 10px;
}

.actions {
  display: flex;
  gap: 15px;
  justify-content: center;
}

.actions button {
  cursor: pointer;
  font-size: 15px;
  background: cornflowerblue;
  border: 1px solid cornflowerblue;
  color: #fff;
  padding: 10px 20px;
}