.message-input-container {
    display: flex;
    gap: 4px;
}

.message-input {
    padding: 12px 8px;
    flex: 1;
    font-size: larger;
    border-radius: var(--border-radius);
}

.send-message {
    padding: 12px 14px;
    background: rgb(46, 122, 207);
    border: 1px solid rgb(46, 122, 207);
    cursor: pointer;
    color: white;
}