.message-list-container {
    margin-bottom: 16px;
    flex: 1;
    max-height: 500px;
    min-height: 300px;
    overflow-y: scroll;
}

.message-list {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    list-style-type: none;  
    padding-left: 0;
}

.message {
    padding: 8px 16px;
    margin-bottom: 8px;
    background: cadetblue;
    border-radius: 20px;
    text-align: left;
}

.own-message {
    background: coral;
    align-self: flex-end;
    text-align: right;
    border-radius: 20px;
}

.sender {
    margin-bottom: 8px;
}