.lobby {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: 10px;
    width: 100%;
}

.rooms {
    display: flex;
    flex-direction: column;
    min-width: 25%;
    border: 1px solid white;
    margin-right: 20px;
    min-height: 50px;
}

.chat {
    display: flex;
    flex-direction: column;
    width: 50%;
    border: 1px solid white;
}

.messages-container {
    
    padding: 16px;
    flex-grow: 1;
    border: 1px solid var(--color-gray);
    border-radius: var(--border-radius);
    overflow: hidden;
    display: flex;
    flex-direction: column;
}

.roomitem {
    font-size: x-large;
}

.roomitem:hover {
    color: blueviolet;
    cursor: pointer;
}

.roomitem.selected {
    color: blueviolet;
}
